import React, { useEffect, useState } from 'react';
import { useHistory } from 'react-router-dom';
import Selectbox from '../components/Selectbox';
import { getProfile, modifyProfile } from '../utils/Api';
import { getUserId } from '../utils/AuthUtil';

const MyInfoPage = ({...props}) => {
    
    const history = useHistory();
    const [userId, setUserId] = useState(0);
    const [email, setEmail] = useState("");
    const [authorId, setAuthorId] = useState(0);
    const [authorName, setAuthorName] = useState("");
    const [name, setName] = useState("");
    const [gender, setGender] = useState("male");
    const [adult, setAdult] = useState(false);

    const max_name_length = 20;
    const max_author_name_length = 20;

    useEffect( () =>  {
        const userId = getUserId();

        getProfile(userId)
            .then( (response) => {
                const profile = response.data.profile;
                setUserId(profile.user_id);
                setAuthorName(profile.author_name);
                setAuthorId(profile.author_id);
                setName(profile.name);
                setGender(profile.gender);
                setEmail(profile.email);
                setAdult(profile.adult==="y" ? true : false)
            })
            .catch ( (error) => {
                //안된다고 경고창
                history.push('/');
            });

    },[]);

    
    const modify = () => {
        const userId = getUserId();
        const newProfile = {
            name : name,
            gender : gender,
            authorName : authorName,
         }
        modifyProfile(userId, newProfile)
            .then( (response) => {
                console.log(response);    
                alert("프로필 변경 성공")
            })
            .catch( (error) => {
                console.log(error);
                alert("프로필 변경 실패")
            })
    }
    return (
        <div>
            <div>
                아이디 {userId}
            </div>
            <div>
                이메일 {email}
            </div>
            <div>
                닉네임
                <input  type="text" 
                        value={name} 
                        onChange={e => setName(e.target.value)} 
                        size = {max_name_length+2}
                        maxLength = {max_name_length}/>
            </div>
            <div>
                <Selectbox name = {"성별"}
                            items = {[
                                {text : "남", value : "male"},
                                {text : "여", value : "famale"},
                                {text : "제 3의 성", value : "thirdGender"},
                                {text : "비공개", value : "notDisclosed"},
                                ]}
                            selectboxName = {"gender-box"}
                            selectedItemValue = {gender}
                            onChangeEvent = {setGender}
                            />
                            
            </div>
            <div>
                작가 번호 : {authorId}
            </div>
            <div>
                필명
                <input  type="text" 
                        value={authorName || ""} 
                        onChange={e => setAuthorName(e.target.value)} 
                        size = {max_author_name_length+2}
                        maxLength = {max_author_name_length}/>
            </div>
            <button onClick={modify}>수정</button>
        </div>
    );
}

export default MyInfoPage;