import { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import { getTicketOfNovelId } from "../utils/Api";

const Ticket = ({novelId}) => {

    const [possessionCount, setPossessionCount] = useState(0);
    const [rentCount, setRentCount] = useState(0);

    const getTicket = () => {
        getTicketOfNovelId(novelId)
        .then( (response) => {
            const ticket = response.data.ticket;
            setRentCount(ticket.rent_count);
            setPossessionCount(ticket.possession_count);
        })
        .catch( (error) => {

        })
    }

    useEffect( () => {
        getTicket();
    },[ novelId] );

    return(
        <div>
            대여권 : {rentCount || 0}개 소장권 : {possessionCount || 0 }개
            <Link to = {{
                pathname : '/ticket/charge',
                search : "?novelId=" + novelId
            }}>
                충전
            </Link>
        </div>
    ); 
}

export default Ticket;