const Radiobox = ({name, items, checkedItemValue, radioboxName, onChangeEvent}) => {
    return (
        <div className={radioboxName}>
            <span>
                {name}
            </span>
            {items.map( (item) => 
                        <label key={item.value}>
                            {item.text} 
                            <input type = "radio" 
                                name = {radioboxName}
                                value={item.value} 
                                checked ={checkedItemValue !== undefined && checkedItemValue === item.value}
                                onChange={e => onChangeEvent(e.target.value)} />
                        </label>)}
        </div>
    )
} 

export default Radiobox