import { useEffect, useState } from 'react';
import { readyKakaoPay } from '../utils/Api';

const CashChargePage = ({...props}) => {


    const [paymentId, setPaymentId] = useState(null);
    const [paymentPrice, setPaymentPrice] = useState(null);
    const [paymentMethodId, setPaymentMethodId] = useState(undefined);

    const [paymentList, setPaymentList] = useState([
        {id : 1, cash_type : "paid", pay_amount : 1000, free_amount : 0, total_amount : 1000, total_price : 1000},
        {id : 2, cash_type : "paid", pay_amount : 2000, free_amount : 0, total_amount : 2000, total_price : 2000},
        {id : 3, cash_type : "paid", pay_amount : 3000, free_amount : 0, total_amount : 3000, total_price : 3000},
        {id : 4, cash_type : "paid", pay_amount : 5000, free_amount : 100, total_amount : 5000, total_price : 5000},
        {id : 5, cash_type : "paid", pay_amount : 10000, free_amount : 0, total_amount : 10000, total_price : 10000},
        {id : 6, cash_type : "paid", pay_amount : 30000, free_amount : 0, total_amount : 30000, total_price : 30000},
        {id : 7, cash_type : "paid", pay_amount : 50000, free_amount : 0, total_amount : 50000, total_price : 50000},
    ]);

    const [paymentMethodList, setPaymentMethodList] = useState([
        {id : 1, name : "카카오페이", request : readyKakaoPay },
    ]);

/* 
    useEffect( () => {
        getCashPayment()
            .then( (response) => {
                const cpList = response.data.cash_payment_list;
                setPaymentList(cpList);
            })
            .catch ( (error) => {
    });
    }, []);
*/
    
    const renderPaymentList = () => {
        return <ul>
                    {paymentList.map( (p) => 
                    <li key = {p.id}>
                        <label onClick = {() => {setPaymentId(p.id); setPaymentPrice(p.total_price)}}>
                            <input type = "radio" 
                                    name = "payment"
                                    value = {p.id} />
                            <span>
                                {p.pay_amount} 캐시
                            </span>
                            {
                            p.free_amount !== 0 ?
                                <span>
                                    + {p.free_amount} 캐시
                                </span>
                                :
                                <></>
                            }
                        </label>
                    </li> )}
               </ul>
    }

    const renderPaymentMethodList = () => {
        return <ul>
                    {paymentMethodList.map( (p) => 
                    <li key = {p.id}>
                        <label onClick = {() => setPaymentMethodId(p.id)}>
                            <input type = "radio" 
                                    name = "payment-method"
                                    value = {p.id} />
                            <span>
                                {p.name}
                            </span>
                        </label>
                    </li> )}
               </ul>
    }
  

    const charge = () => {
        if(!paymentMethodId || !paymentId){
            alert("결재 금액 및 결제 수단을 선택해주세요")
            return
        }
        const paymentMethod = paymentMethodList.find( i => i.id === paymentMethodId);
        paymentMethod.request(paymentId)
            .then( (response) => {
                window.open( response.data.redirect_url, "_blank", "channelmode=no,height=600,width=500,", true);
            })
            .catch( (error) => {

            } )

    }

    return ( 
                <div>
                    {renderPaymentList()}
                    {renderPaymentMethodList()}
                    <div>
                        <span>
                            결제 금액 
                        </span>
                        <span>
                            {paymentPrice}
                        </span>
                        <span>
                            원
                        </span>
                    </div>
                    <button onClick = {charge}>
                        충전
                    </button>
                </div>
            );
}

export default CashChargePage;