import {React} from 'react';
import SocialLogin from '../components/SocialLogin';
import { Link } from 'react-router-dom';
import emailSignUpImg from '../img/emailSignUp.png';

const SignUpPage = () => {
    
    return (
        <div>
            <Link to='/signup/emailForm' > <img src = {emailSignUpImg}  height={46} width = {191}/> </Link>
            <SocialLogin />
        </div>
    );
}

export default SignUpPage;