import React, { useState } from 'react';
import Selectbox from '../components/Selectbox';
import { createEpisode } from '../utils/Api';
import DatePicker from "react-datepicker";
import 'react-datepicker/dist/react-datepicker.css'
import {ko} from "date-fns/esm/locale"
import { useHistory } from 'react-router-dom';
import Radiobox from '../components/Radiobox';

const EpisodeRegisterPage = ({novelId}) => {
    
    const history = useHistory();
    const [title, setTitle] = useState('');
    const [content, setContent] = useState('');
    const [authorComment, setAuthorComment] = useState('');

    const [adult ,setAdult] = useState(false);

    const [open, setOpen] = useState("now"); 
    const [openDate, setOpenDate] = useState(new Date());
    const [openHours, setOpenHours] = useState(openDate.getHours());
    const [openMinutes, setOpenMinutes] = useState(openDate.getMinutes());

    const register = () => {
        createEpisode(novelId, setEpisode())
        .then( (response) => {;
            history.push("/novel/"+novelId);
        })
        .catch( (error) => {

        })
    }

    const setEpisode = () => {
        let hidden;
        let date;
        if(open === "hidden"){
            hidden = "y";
            date = undefined;
        }else if( open === "now"){
            hidden = "n";
            date = undefined;
        }else if( open === "reservation"){
            hidden = "n";
            date = openDate;
            date.setHours(openHours);
            date.setMinutes(openMinutes);
        }

        return {
            title : title,
            content : content,
            authorComment : authorComment,
            adult : adult,
            hidden : hidden,
            openDate : date
        }
    }

    const changeOpenHours = (hours) => {
        if(typeof hours == "string")
            hours = parseInt(hours);
        setOpenHours(hours);
    } 

    const changeOpenMinutes = (minutes) => {
        if(typeof minutes == "string")
            minutes = parseInt(minutes);
        setOpenMinutes(minutes);
    } 

    const renderHoursBox = () => {
        const now = new Date();
        
        let startIndex = 0;
        if (now.getMonth() ===  openDate.getMonth() 
            && now.getDate() === openDate.getDate()){
            startIndex = now.getHours();
        }
        const items = Array.from( {length : 24}, ( item, index )=>{ 
            return { 
                    text : index  + "시",
                    value : index 
                    };
            })
            .filter( (item, index) => startIndex <= index);

        return <Selectbox  
                            items = {items} 
                            selectedItemValue = {openHours}
                            selectboxName = {'hours-box'}
                            onChangeEvent = {changeOpenHours} />
    }

    const renderMinutesBox = () => {
        const now = new Date();
        let startIndex = 0;
        if (now.getMonth() ===  openDate.getMonth() 
            && now.getDate() === openDate.getDate()
            && now.getHours() === openHours){
            startIndex = now.getMinutes();
        }
        const items = Array.from( {length : 60}, ( item, index )=>{ 
            return { 
                    text : index  + "분",
                    value : index 
                    };
                })
                .filter( (item, index) => startIndex <= index);

        return <Selectbox   
                            items ={items} 
                            selectedItemValue = {openMinutes}
                            selectboxName = {'minutes-box'}
                            onChangeEvent = {changeOpenMinutes} />
    }
    return ( 
                <div>
                    <div>
                        <input
                            type = 'text'
                            placeholder='제목'
                            onChange={e => setTitle(e.target.value)}
                            />
                    </div>

                    <div>
                        <Selectbox  name = {'열람'} 
                                    items = {[
                                        {text : "전체 이용", value : false },
                                        {text : "성인 전용", value : true }
                                    ]} 
                                    selectedItemValue = {adult}
                                    selectboxName = {'adult-box'}
                                    onChangeEvent = {setAdult} />
                    </div>
                    <div>
                        <Radiobox   name={"공개 방식"} 
                                    items = {[
                                        {text : "예약", value : "reservation"},
                                        {text : "즉시", value : "now"},
                                        {text : "비공개", value : "hidden"}
                                    ]}
                                    checkedItemValue = {open}
                                    radioboxName = {"use-open-date"}
                                    onChangeEvent = {setOpen}
                                    />
                    </div>
                    <div>
                        <DatePicker 
                                    locale={ko} 
                                    selected={openDate} 
                                    minDate={new Date()}
                                    maxDate={ new Date(openDate.getFullYear()+1, openDate.getMonth(), openDate.getHours())}
                                    onChange={(date) => setOpenDate(date)} />
                        {renderHoursBox()}
                        {renderMinutesBox()}
                    </div>
                    <div>
                        <textarea
                            placeholder="내용" 
                            value={content} 
                            onChange={e => setContent(e.target.value)}
                            rows = '10'
                            cols = '100' 
                            maxLength = {1000}/>
                    </div>
                    <div>
                    <textarea
                            placeholder="작가의 말" 
                            value={authorComment} 
                            onChange={e => setAuthorComment(e.target.value)}
                            rows = '10'
                            cols = '100' 
                            maxLength = {1000}/>
                    </div>
                    <div>
                        <button onClick={register}>
                            등록
                        </button>
                    </div>
                </div>
            );
}

export default EpisodeRegisterPage;