import { Link } from "react-router-dom"
import TmpImg from "../img/tmp.png"

const EpisodeInfo = ({episodeInfo}) => {
    return (
        <div>
            <div>
                <Link to={'/episode/' + episodeInfo.episode_id} >
                    <img 
                        src={TmpImg}
                        alt='cover' />
                </Link>
            </div>
            <div>
                <div>
                    {episodeInfo.title}
                </div>
                <div>
                    {episodeInfo.description}
                </div>
            </div>           
            <div>
                {episodeInfo.free ? "무료" : "유료"}
            </div>
            <div>
                {episodeInfo.open_date}
            </div>
        </div>
    )
} 

export default EpisodeInfo