import { useEffect, useState } from 'react';
import { useHistory } from 'react-router-dom';
import { getEpisodeInfoOfNovelId } from '../utils/Api';
import EpisodeInfo from './EpisodeInfo';
import PageButtons from './PageButtons';

const EpisodeInfoList = ({novelId, ...props}) => {

    const history = useHistory(); 
    const [episodeInfoList, setEpisodeInfoList] = useState([]);
    const [totalEpisodeInfoCount, setTotalEpisodeInfoCount] = useState(0);
    const [totalEpisodeInfoPage, setTotalEpisodeInfoPage] = useState(0);

    const [pageInfo, setPageInfo] = useState({ 
        page : 0,
        size : 20,
        sort : 'newest'
     });

    useEffect( () => {
        getEpisodeInfoList()
    }, [novelId, pageInfo]);

     const getEpisodeInfoList = () => {
        getEpisodeInfoOfNovelId(novelId, pageInfo)
            .then( (response) => {
                const episode_info_list = response.data.episode_info_list;

                episode_info_list.map( (item) => {
                    item.adult = item.adult === "y" ? true : false; 
                    item.free = item.free === "y" ? true : false;
                    item.hidden = item.hidden === "y" ? true : false;
                })
                setEpisodeInfoList(episode_info_list);
                setTotalEpisodeInfoCount(response.data.total_episode_info_count);
                setTotalEpisodeInfoPage(response.data.total_episode_info_page);
            })
            .catch( (error) => {
                // 이용 불가 경고창
                history.push('/');
            });
    }

    const renderEpisodeInfoList = () => {
        return episodeInfoList.map( (item, index) =><EpisodeInfo key = {index}
                                                                 episodeInfo = {item}
                                                                /> )
    }
    return ( 
                <div>
                    <div>
                        {renderEpisodeInfoList()}
                    </div>
                    <div>
                        <PageButtons 
                        currentPage = {pageInfo.page} 
                        totalPage = {totalEpisodeInfoPage} 
                        setPage = { (newPage) => setPageInfo( {...pageInfo, page : newPage})  } />
                    </div>
                </div>
            );  
}

export default EpisodeInfoList;