import React, { useEffect, useState } from 'react';
import { Link, useHistory } from 'react-router-dom';
import NovelInfo from '../components/NovelInfo';
import PageButtons from '../components/PageButtons';
import { getNovelOfUser } from '../utils/Api';

const MyCreationPage = ({...props}) => {
    const history = useHistory();

    const [novelList, setNovelList] = useState([]);
    const [totalNovelCount, setTotalNovelCount] = useState(0);
    const [totalNovelPage, setTotalNovelPage] = useState(0);

    const [pageInfo, setPageInfo] = useState({  page : 0,
                                                size : 10,
                                                sort : 'newest'
                                            });

    useEffect( () => {
        getNovelOfUser(pageInfo)
            .then( (response) => {
                const data = response.data;
                setNovelList(data.novel_list);
                setTotalNovelCount(data.total_novel_count);
                setTotalNovelPage(data.total_novel_page);
            } )
            .catch( (error) => {
                // 오류출력후
                history.push('/');
            })
    }, [pageInfo]); 


    const printNovelInfos = () => {
        if(totalNovelCount === 0)
            return notExistMessage();
        else
            return novelInfos();
    };

    const notExistMessage = () => {
        return <p>등록된 작품이 없습니다.</p>
    };

    const novelInfos = () => {
        return novelList.map( (novel, index) => 
            <div key = {novel.novel_id}>
                <div>
                    <NovelInfo num = {index} 
                                novel = {novel}/> 
                </div>
                <div>
                    <Link to={'/my_creation/write/' + novel.novel_id}>
                        <span>
                            글쓰기
                        </span>
                    </Link>
                    <Link to={'/my_creation/manage/' + novel.novel_id}>
                        <span>
                            작품 관리
                        </span>
                    </Link>
                </div>
            </div>
        );
    }


    return ( 
            <div>
                <div>
                    <Link to = '/my_creation/register'>
                        <button>
                            새 작품 등록
                        </button>
                    </Link>
                </div>
                <div>
                    {printNovelInfos()}
                </div>                  
                <div>
                <PageButtons currentPage = {pageInfo.page} totalPage = {totalNovelPage} setPage = { (newPage) => setPageInfo({...pageInfo, page : newPage})} />
                </div>
            </div>
            );
}

export default MyCreationPage;