import { Link, useHistory } from 'react-router-dom';
import { logout } from '../utils/Api';

const AccountBox = ({unAuthenticate , ...props}) => {
    const history = useHistory();

    const onLogout = () => {
        logout()
            .then( (response) => {
                unAuthenticate();
            })
            .catch((error) => {
                unAuthenticate();
            });
        history.push('/');
    }   
    return (
            <div>  
                    <Link to = "/my_info">
                        <div>내 정보관리</div>
                    </Link>
                    <Link to = '/my_creation'>
                        <div>내 작품관리</div>
                    </Link>
                    <Link to = '/cash/charge'>
                        <div>캐시 충전</div>
                    </Link>

                    <div onClick={onLogout}>
                        로그아웃
                    </div>
            </div>
            );
}

export default AccountBox;