import { approvalKakaoPay } from '../utils/Api';

const KakaoPayRedirectHandler = ({pg_token, result, ...props}) => {

    const returnUrl = window.location.protocol + "//" + "localhost:3000/cash/charge/result";

    console.log("@@",pg_token, result, props);

    if(result === "approval"){

        approvalKakaoPay(pg_token)
            .then( (response) => {
                console.log("r", response);
                console.log(window.opener);
                window.opener.location.href = returnUrl + "?tid=" + "111";
                window.close();
            })  
            .catch( (error) => {
                console.log("e",error);
                window.opener.location.href = returnUrl + "?tid=" + "333";
                window.close();
            });
            //window.opener.location.href = "localhost:3000/cash/charge/success?pg_token=" + pg_token + "&payment_method=" + 1;
        }else if(result === "cancel"){
            console.log(pg_token, result);
        }else if(result === "fail"){
            console.log(pg_token, result);
        }
    
        

    return (<div></div>);
} 

export default KakaoPayRedirectHandler;