export function searchNovel(keyword, pageInfo){
    return notRequiredAuthInstance.get('/api/novels/keyword/' + keyword, 
        {
            params : {
                page : pageInfo.page,
                size : pageInfo.size,
                sort : pageInfo.sort,
            }
        });
}

export function createNovel(novel){
    return requiredAuthInstance.post('/api/novels', 
                {
                    title : novel.title,
                    description : novel.description,
                    hidden : novel.hidden === true || novel.hidden === "true" ? "y" : "n",
                    exclusive : novel.exclusive === true || novel.exclusive === "true" ? "y" : "n",
                    age_grade : novel.ageGrade,
                    genre : novel.genres,
                    serial_cycle : novel.serialCycles,
                    
                }
    );
}

export function modifyNovel(novelId, novel){
        return requiredAuthInstance.put('/api/novels', 
                {
                        novel_id : novelId,
                        title : novel.title,
                        description : novel.description,
                        hidden : novel.hidden === true || novel.hidden === "true" ? "y" : "n", //아니 왜 자꾸 문자열로 바뀌는 거여??
                        exclusive : novel.exclusive === true || novel.exclusive === "true" ? "y" : "n",
                        age_grade : novel.ageGrade,
                        genre : novel.genres,
                        serial_cycle : novel.serialCycles,
                        
                }
        );
}

export function getEpisode(episodeId){
    return requiredAuthInstance.get('/api/episodes/'+episodeId);
}

export function getEpisodeInfo(episodeId){
    return notRequiredAuthInstance.get( '/api/episodes'.concat('/').concat(episodeId).concat('/info') );
}

export function getEpisodeInfoOfNovelId(novelId, pageInfo){
    return axios.get('/api/episodes/info',
            {
                params : {
                    novelId : novelId,
                    page : pageInfo.page,
                    size : pageInfo.size,
                    sort : pageInfo.sort,
                }
            }
    );
}

export function createEpisode(novelId, episode){
    return requiredAuthInstance.post('/api/episodes',
            {
                novel_id : novelId,
                title : episode.title,
                content : episode.content,
                author_comment : episode.authorComment,
                adult : episode.adult === true || episode.adult === "true" ? "y" : "n",
                hidden : episode.hidden,
                open_date : episode.openDate && ChangeDateFormat(episode.openDate)
            });
}
