import { useEffect, useRef, useState } from 'react';
import { Link } from 'react-router-dom';
import titleImg from '../img/title.png';
import AccountBox from './AccountBox';
import SearchBar from './SearchBar';
import style from '../css/components/AppHeader.module.css';
import ProfileImg from '../img/profile.png';

const AppHeader = ({authenticated, unAuthenticate, ...props} ) => {
    const ref = useRef(null);
    const [showProfile, setShowProfile] = useState(false);

    const changeShowProfile = () => {
        setShowProfile(!showProfile);
    };

    const handleClickOutside = (e) => {
        if (ref.current && !ref.current.contains(e.target)) 
            setShowProfile(false);
      };

    useEffect(() => {
        document.addEventListener('click', handleClickOutside);
        return () => {
            document.removeEventListener('click', handleClickOutside);
        };
    }, []);
    
    return (
        <div className={style.header}>

            <div className={style.title}>
                <Link to="/" >
                    <img 
                        className={style.title__img}
                        src={titleImg}
                        alt='title'
                    />
                </Link>
            </div>

            <SearchBar />

            {
            authenticated ?
                <div className={style['account-box']} ref={ref}>
                    <div className={style['account-box__profile']}
                        onClick = {changeShowProfile} >   
                        <img 
                            className={style['account-box__profile__img']}
                            src={ProfileImg}
                            alt='profile'>
                        </img>
                    </div>
                    <div >
                        { showProfile && 
                            <AccountBox unAuthenticate={unAuthenticate}/>  }
                    </div>
                </div>
                
            :
                <div className={style['auth-box']}>
                    < Link to={{
                                pathname: '/login'}} 
                            className={style['auth-box__item']}>
                        로그인
                    </Link>
                    < Link to='/signup' className={style['auth-box__item']}>
                        회원가입
                    </Link>
                </div>
            }
        </div>
    );

}

export default AppHeader;