import React, { useEffect, useState } from 'react';
import { getNovelById } from '../utils/Api';
import NovelInfo2 from '../components/NovelInfo2';
import { useHistory } from 'react-router-dom';
import EpisodeInfoList from '../components/EpisodeInfoList';
import Ticket from '../components/Ticket';

const NovelPage = ({id, authenticated, ...props}) => {

    const history = useHistory();
    const [novel, setNovel] = useState({});

    const getNovel = () => {
        getNovelById(id)
            .then( (response) => {
                setNovel(response.data.novel);
            })
            .catch( (error) => {
                // 이용 불가 경고창
                history.push('/');

            });
    }


    useEffect( () => {
        getNovel();
    },[id]);
    
    return ( 
        <div>
            <NovelInfo2 novel={novel}/>
            { ( authenticated && novel.free === "n" ) && <Ticket novelId = {id}  />}
            <EpisodeInfoList  novelId = {id} />
        </div>
        );
}

export default NovelPage;