import React from 'react';
import { Redirect, Route } from 'react-router-dom';
import { isExistRefreshToken } from '../utils/AuthUtil';

function PrivateRoute ({authenticated, render, ...rest }) {
    console.log(rest);
    return (
        <Route
            {...rest}
            render = { props => 
                isExistRefreshToken() ? 
                        render(props)
                    :
                        <Redirect to={{
                            pathname: '/', 
                        }} />

         
                
            }
        />
    )
}

export default PrivateRoute;