import React, { useEffect, useState } from 'react';
import { useHistory } from 'react-router-dom';
import NovelForm from '../components/NovelForm';
import { getNovelById, modifyNovel } from '../utils/Api';

const NovelManagePage = ({id, ...props}) => {
    const history = useHistory();
    const [novel,setNovel] = useState({}); 

    useEffect( () => {
        getNovelById(id)
            .then( (response) => {
                const novelRes = response.data.novel;
                novelRes.hidden = novelRes.hidden === "y" ? true : false;
                novelRes.exclusive =  novelRes.exclusive === "y" ? true : false;
                setNovel(novelRes);
            })
            .catch((error) => {
                console.log(console.error);
            });
    }, [id]);


    const modify = (novel) => {
        modifyNovel(id, novel)
            .then( (response) => {
                history.push('/my_creation');
            })
            .catch( (error) => {
                console.log(error);
            })
        
    };
    
    return (
        <div>
            <NovelForm  novel = {novel}
                        onsubmit = {modify}
                        submitBtnName = {'수정'}/>
        </div>
    )
}

export default NovelManagePage;