import React, { useEffect, useState } from 'react';
import Selectbox from '../components/Selectbox';
import 'react-datepicker/dist/react-datepicker.css'
import { useHistory } from 'react-router-dom';
import { getEpisode } from '../utils/Api';

const EpisodeModifyPage = ({id}) => {
    
    const history = useHistory();
    const [title, setTitle] = useState('');
    const [content, setContent] = useState('');
    const [authorComment, setAuthorComment] = useState('');

    const [adult ,setAdult] = useState(false);

    const [hidden, setHidden] = useState(false); 


    useEffect( () => {
        getEpisode(id)
        .then( (response) => {
            const episode = response.data.episode;
            setTitle(episode.title);
            setContent(episode.content);
            setAuthorComment(episode.author_comment);
            setAdult(episode.adult === "y" ? true : false);
            setHidden(episode.hidden === "y" ? true : false);
        })
        .catch( (error) => {

        })
        
    },[id])
    const modify = () => {
        
    }

    return ( 
                <div>
                    <div>
                        <input
                            type = 'text'
                            placeholder='제목'
                            onChange={e => setTitle(e.target.value)}
                            />
                    </div>

                    <div>
                        <Selectbox  name = {'열람'} 
                                    items = {[
                                        {text : "전체 이용", value : false },
                                        {text : "성인 전용", value : true }
                                    ]} 
                                    selectedItemValue = {adult}
                                    selectboxName = {'adult-box'}
                                    onChangeEvent = {setAdult} />
                    </div>
                    <div>
                    </div>
                    <div>
                        <textarea
                            placeholder="내용" 
                            value={content} 
                            onChange={e => setContent(e.target.value)}
                            rows = '10'
                            cols = '100' 
                            maxLength = {1000}/>
                    </div>
                    <div>
                    <textarea
                            placeholder="작가의 말" 
                            value={authorComment} 
                            onChange={e => setAuthorComment(e.target.value)}
                            rows = '10'
                            cols = '100' 
                            maxLength = {1000}/>
                    </div>
                    <div>
                        <button onClick={modify}>
                            수정
                        </button>
                    </div>
                </div>
            );
}

export default EpisodeModifyPage;