const Checkbox = ({name, items, checkedItemValues, checkboxName, onChangeEvent}) => {
    return (
        <div className={checkboxName}>
            <span>
                {name}
            </span>
            {items.map( (item) => 
                        <label key={item.value}>
                            {item.text} 
                            <input type = "checkbox" 
                                name = {checkboxName}
                                value={item.value} 
                                checked ={checkedItemValues!==undefined&&checkedItemValues.includes(item.value)}
                                onChange={e => onChangeEvent(e.target.value)} />
                        </label>)}
        </div>
    )
} 

export default Checkbox