import React, { useEffect, useState } from "react";
import { useHistory } from "react-router-dom";
import { getEpisode } from "../utils/Api";


const EpisodePage = ({id}) => {
    const history = useHistory();

    const [episode, setEpisode] = useState({});

    useEffect( () => {
        getEpisode(id)
        .then( (response) => {
            setEpisode(response.data.episode);
            console.log(episode);
        })
        .catch( (error) => {
            // 넌 못봄 이라고 알리고
            history.push("/");
        });
    },[id]);

    return(
            <div>
                <div>
                    제목 {episode.title}
                </div>
                <div>
                    내용 {episode.content}
                </div>
                <div>
                    작가의 말 {episode.author_comment}
                </div>
            </div>
    );

}

export default EpisodePage;